@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 72px 0 10px;
    }
	
	.qodef-comment {
		margin: 0 0 25px;
        padding: 0 0 25px;
		border-bottom: 1px dashed $default-border-color;
		
		&.qodef-pingback-comment {
			
			.qodef-comment-text {
				padding: 0;
			}
		}
	}

    .qodef-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include qodefRelativeHolderLayout();
        }

        .children {
            padding: 0 0 0 91px;
        }
    }

    .qodef-comments-title {
	    padding: 0 0 25px;
        margin: 0 0 25px;
	    border-bottom: 1px dashed $default-border-color;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        display: block;
        top: 3px;
        left: 0;
        width: 60px;
        height: 60px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 85px;
        min-height: 60px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: 12px 0 16px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
	        margin: 3px 10px 0 0;
            font-size: 12px;
	        font-weight: 700;
	        text-transform: uppercase;
	        letter-spacing: .05em;
            color: #191919;

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
        }

        .qodef-comment-date {
            display: none;
        }

        .qodef-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .qodef-comment-form-inner {
        padding: 7px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 24px;
    }

	label {
		display: inline-block;
		margin: 0 0 1px;
		font-weight: 700;
		letter-spacing: -.025em;
		color: $default-heading-color;
	}

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {
	        
            .qodef-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
	display: block;
	max-width: 560px;
}

#submit_comment,
.post-password-form input[type='submit'] {
	@include qodefButtonDefaultStyle();
	@include qodefButtonSize();
	@include qodefButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include qodefButtonSolidHoverColor();
	}
}

p.form-submit {
	margin: 14px 0 0;
}

#respond textarea,
.qodef-style-form textarea {
	display: block;
	padding: 16px 18px;
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Contact Form 7 styles - begin
   ========================================================================== */

.wpcf7-form {
	position: relative;
	box-shadow: 0px 5px 21px 0px rgba(83, 246, 198, 0.37);

	.qodef-dark-skin &,
	.qodef-no-shadow & {
		box-shadow: none;
	}
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;

		margin: 0 0 36px;
		padding: 7px 0;
		font-size: 14px;
		line-height: 21px;
		border: none;
		border-bottom: 1px solid $default-heading-color;

		&:hover,
		&:focus {
			color: $default-heading-color;
			border-color: $additional-color;
		}

		footer & {
			margin: 0;
			color: #fff;
			border-bottom: 1px solid rgba(#e1e1e1, .5);

			&:hover,
			&:focus {
				color: #fff;
			}
		}
	}

	&.wpcf7-textarea {
		display: block;
	}

	&.wpcf7-select {
		-webkit-appearance: menulist;
	}

	&.wpcf7-checkbox {
		margin: 0 0 36px;

		span.wpcf7-list-item {
			margin: 0;

			.wpcf7-list-item-label {
				vertical-align: text-bottom;
				color: inherit;
			}
		}
	}
}

.qodef-cf7-element-shorter-holder {

	.wpcf7-form-control {
		&.wpcf7-text,
		&.wpcf7-number,
		&.wpcf7-date,
		&.wpcf7-textarea,
		&.wpcf7-select,
		&.wpcf7-checkbox,
		&.wpcf7-quiz {
			display: inline-block;
			margin: 0 0 28px;
		}
	}
	
	p {
		margin: 0;
	}
}

input.wpcf7-form-control.wpcf7-submit {
	@include qodefButtonDefaultStyle();
	@include qodefButtonSize(large);
	@include qodefButtonSolidColor();
	cursor: pointer;

	&:hover {
		@include qodefButtonSolidHoverColor();
	}
}

.qodef-wpcf7-form-simple {

	.wpcf7-form-control {
		&.wpcf7-text,
		&.wpcf7-number,
		&.wpcf7-date,
		&.wpcf7-textarea,
		&.wpcf7-select,
		&.wpcf7-quiz {
			padding-right: 60px;
		}

		&.wpcf7-submit {
			position: absolute;
			top: 8px;
			right: 1px;
			display: inline-block;
			vertical-align: top;
			margin: 0;
			padding: 0;
			font-family: $default-heading-font;
			font-size: 13px;
			font-weight: 700;
			text-align: center;
			text-transform: uppercase;
			letter-spacing: .05em;
			color: inherit;
			background-color: transparent;
			border: 0;
			border-radius: 0;
			outline: none;
			cursor: pointer;
			box-sizing: border-box;
			@include qodefTransition(all .3s ease-out);

			&:hover {
				color: $first-main-color;
				background-color: transparent;
			}
		}
	}
}

.qodef-wpcf7-form-custom {

    .qodef-form-inner {
        width: 100%;
	    margin: 0;
        padding: 21px 0;
	    background-color: #fff;

        .wpcf7-form-control {

	        &.wpcf7-text {
		        position: relative;
		        display: inline-block;
		        vertical-align: top;
		        float: left;
		        width: 80%;
		        margin: 0;
		        padding: 26px 58px;
		        font-size: 14px;
		        line-height: 32px;
		        font-weight: 400;
		        color: $default-heading-color;
		        background-color: transparent;
		        border: 0;
		        border-right: 2px solid $first-main-color;
		        border-radius: 0;
		        outline: none;
		        box-sizing: border-box;

		        &:focus {
			        color: $default-heading-color;
		        }

		        @include qodefPlaceholder {
			        color: inherit;
		        }

		        @include ipad-portrait {
			        width: 75%;
		        }

		        @include phone-landscape {
			        width: 70%;
		        }
	        }

	        &.wpcf7-submit {
		        position: relative;
	            display: inline-block;
	            vertical-align: top;
	            float: left;
	            margin: 0;
	            padding: 26px 0;
	            width: 20%;
	            font-family: $default-heading-font;
		        font-size: 13px;
	            line-height: 32px;
	            font-weight: 700;
	            text-align: center;
	            text-transform: uppercase;
	            letter-spacing: .05em;
	            color: $default-text-color;
	            background-color: transparent;
	            border: 0;
	            border-radius: 0;
	            outline: none;
	            cursor: pointer;
	            box-sizing: border-box;
	            @include qodefTransition(all .3s ease-out);

	            &:hover {
	                color: $first-main-color;
	            }

		        @include ipad-portrait {
			        width: 25%;
		        }

		        @include phone-landscape {
			        width: 30%;
		        }
	        }
        }
    }

	.ajax-loader {
		display: none !important;
	}
}

.qodef-dark-skin {

	.wpcf7-form-control {
		&.wpcf7-text,
		&.wpcf7-number,
		&.wpcf7-date,
		&.wpcf7-textarea,
		&.wpcf7-select,
		&.wpcf7-quiz {
			color: #fff;
			border-bottom: 1px solid rgba(#636363, .5);

			&:hover,
			&:focus {
				color: #fff;
				border-color: $additional-color;
			}
		}

		&.wpcf7-checkbox {

			span.wpcf7-list-item {

				.wpcf7-list-item-label {
					color: #fff;
				}
			}
		}
	}

	.qodef-wpcf7-form-custom {

	    .qodef-form-inner {
	        background-color: #111;

	        .wpcf7-form-control {

		        &.wpcf7-text {
			        color: #fff;

			        &:focus {
				        color: #fff;
			        }
		        }

		        &.wpcf7-submit {
			        color: #fff;

		            &:hover {
		                color: $first-main-color;
		            }
		        }
	        }
	    }
	}
}

.qodef-cf7-btn-wrapper {
	position: relative;
	display: inline-block;
	@include qodefButtonDefaultStyle();
	@include qodefButtonSize(large);
	@include qodefButtonSolidColor();
	cursor: pointer;

	input.wpcf7-form-control.wpcf7-submit {
		border: none;
		background: transparent;
		padding: 0;
		color: currentColor;
		@include qodefTransitionTransform(.35s cubic-bezier(0.23, 1, 0.32, 1));
	}

	.ajax-loader {
		position: absolute;
		top: calc(50% - 8px);	
	}

	.qodef-svg-icon {
		position: absolute;
		top: calc(50% - 4px);
		left: 80%;
		height: 7px;
		@include qodefTransform(translate(-5px, 0));
		opacity: 0;
		@include qodefTransitionTransform(.0s .1s, opacity .1s);
		will-change: transform, opacity;
	}

	&:hover {
		input.wpcf7-form-control.wpcf7-submit {
			@include qodefTransform(translateX(-5px));
		}

		.qodef-svg-icon {
			@include qodefTransform(translate(0%, 0));
			opacity: 1;
			@include qodefTransitionTransform(.2s cubic-bezier(0.19, 0.5, 0.46, 0.88), opacity .2s);
		}
	}
}
/* ==========================================================================
   #Contact Form 7 styles - end
   ========================================================================== */

/* ==========================================================================
	#Rev Slider - start
	========================================================================== */

	#qodef-main-rev-slider-holder .rev_slider_wrapper {
		background-image: url("../img/preload_pattern.png") !important;
		background-repeat: repeat;
		background-color: #000 !important;
		@include qodefAnimation(preload-background 40s infinite linear 0s);
  	}
  
