/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.qodef-slider-is-moving {
		
		.owl-item {
			
			.qodef-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.qodef-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include qodefTransform(translateY($pos));
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: #fff;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			@include qodefTransform(translateY(-50%));
			@include qodefTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}

			.qodef-custom-dark-skin & {
				color: $default-heading-color;

				&:hover {
					color: $first-main-color;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			width: 13px;
			height: 13px;
			padding: 3px;
			margin: 0 8px;
			background: none;
			border: 1px solid transparent;
			border-radius: 0;
			outline: none;
			box-sizing: border-box;
			-webkit-appearance: none;
			@include qodefTransition(border-color .2s ease-out);
			
			span {
				display: inline-block;
				vertical-align: text-top;
				width: 5px;
				height: 5px;
				background-color: #313131;
				@include qodefTransition(background-color .2s ease-out);
			}

			&:hover {
				border-color: #313131;
			}

			&.active {
				border-color: #d7d7d7;

				span {
					background-color: #d7d7d7;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
