/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .qodef-wrapper {
        padding: 0 !important;
    }
    
    .qodef-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.qodef-content {
			height: calc(100vh - 32px);
		}
	}

    .qodef-content-inner {
	    @include qodefTableLayout();
    }

    .qodef-page-not-found {
	    @include qodefTableCellLayout();
	    padding: 0 0 0 15%;

	    @include ipad-landscape {
			padding: 0 0 0 5%;
		}

	    .qodef-404-tagline {
		    display: inline-block;
		    margin: 0 0 22px;
		    font-family: $additional-font;
		    font-size: 14px;
		    font-style: italic;
		    letter-spacing: .1em;
		    color: $first-main-color;
	    }

        .qodef-404-title {
            margin: 0;
	        font-size: 200px;
	        line-height: .75em;

	        @include smaller-phone-portrait {
		        font-size: 150px;
	        }
        }
	
	    .qodef-404-subtitle {
            margin: 17px 0 0;
		    font-size: 60px;
        }

	    .qodef-404-search-table {
	        display: table;
	        width: 100%;
	        height: 100%;
	    }

	    .qodef-404-search-cell {
	        @include qodefTableCellLayout();
	    }

	    .qodef-form-holder-inner {
	        position: relative;
	        width: 95%;
	    }

	    .qodef-search-form-inner {
	        width: calc(100% - 202px);
		    margin: 46px 0 0;
	        padding: 13px 0;
		    background-color: #fff;
	        box-shadow: 0px 5px 21px 0px rgba(83, 246, 198, 0.37);

		    @include ipad-landscape {
			    width: 100%;
		    }

	        .qodef-search-field {
	            position: relative;
	            display: inline-block;
	            vertical-align: top;
	            float: left;
	            width: calc(100% - 170px);
	            margin: 0;
	            padding: 17px 38px;
	            font-size: 16px;
	            line-height: 32px;
	            font-weight: 400;
	            color: $default-heading-color;
	            background-color: transparent;
	            border: 0;
	            border-right: 2px solid $first-main-color;
	            border-radius: 0;
	            outline: none;
	            box-sizing: border-box;

	            &:focus {
	                color: $default-heading-color;
	            }

	            @include qodefPlaceholder {
	                color: inherit;
	            }

		        @include ipad-landscape {
			        padding: 17px 28px;
		        }

	            @include phone-portrait {
	                width: calc(100% - 100px);
	                padding: 17px 14px;
	            }

	            @include smaller-phone-portrait {
	                width: calc(100% - 80px);
	                padding: 17px 4px;
	            }
	        }

	        .qodef-search-submit {
	            position: relative;
	            display: inline-block;
	            vertical-align: top;
	            float: left;
	            margin: 0;
	            padding: 17px 38px;
	            width: 170px;
	            font-family: $default-heading-font;
	            line-height: 32px;
	            font-weight: 700;
	            text-align: center;
	            text-transform: uppercase;
	            letter-spacing: .05em;
	            color: $default-text-color;
	            background-color: transparent;
	            border: 0;
	            border-radius: 0;
	            outline: none;
	            cursor: pointer;
	            box-sizing: border-box;
	            @include qodefTransition(all .3s ease-out);

	            @include ipad-landscape {
			        padding: 17px 28px;
		        }

	            @include phone-portrait {
	                width: 100px;
	                padding: 17px 14px;
	            }

	            @include smaller-phone-portrait {
	                width: 80px;
	                padding: 17px 4px;
	            }

	            &:hover {
	                color: $first-main-color;
	            }

	            &.qodef-search-submit-icon-pack {
	                font-size: 13px;
	            }

	            &.qodef-search-submit-svg-path {

	                > *,
	                path {
	                    fill: currentColor;
	                    stroke: currentColor;
	                }
	            }
	        }
	    }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 40px;
    right: 25px;
    bottom: 25px;
    z-index: 999999;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include qodefTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include qodefTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 40px;
        height: 40px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: $default-heading-color;
		background-color: $additional-color;
		@include qodefTransition(background-color .3s);
	}

	i {
		display: inline-block;
		@include qodefTransitionTransform(.15s cubic-bezier(0.19, 0.5, 0.46, 0.88));
	}
	
	&.qodef-dark {
		
		> span {
			color: #fff;
