/* ==========================================================================
   #Background Text styles - begin
   ========================================================================== */
.qodef-row-bg-text-holder {
	position: absolute;
	top: 0;
	visibility: hidden;

	.touch & {
		visibility: visible;
	}

	.qodef-row-bg-text-inner {
		box-sizing: border-box;

		.no-touch & {
			overflow: hidden;
		}
	}

	.qodef-char-mask {
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
	}

	.qodef-char {
		display: inline-block;
	}

	&.qodef-left-aligned {
		left: 0;
		
		.qodef-row-bg-text-inner {
			padding-left: 2vw;
		}

		.no-touch & .qodef-char {
			opacity: 0;
			@include qodefTransform(translate3d(-100%,0,0));
		}
	}

	&.qodef-right-aligned {
		right: 0;
		text-align: right;

		.qodef-row-bg-text-inner {
			padding-right: 2vw;
		}

		.no-touch & .qodef-char {
			opacity: 0;
			@include qodefTransform(translate3d(100%,0,0));
		}
	}

	.qodef-row-bg-text {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		font-weight: 700;
		font-size: 20vw;
		line-height: 1.15em;
		opacity: .2;
		overflow: hidden;
		
		&:after {
			content: '';
			@include qodefAbsoluteHolderLayout();
			z-index: 1;
			background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAIAAAACCAMAAABFaP0WAAAABlBMVEUAAAD///+l2Z/dAAAAAXRSTlMAQObYZgAAAA5JREFUCNdjYGRgYGQEAAAOAATa5WfvAAAAAElFTkSuQmCC);
			background-size: 2px 2px;
			@include qodefTransform(scale(1.5));
		}
	}
	
	.no-touch &.qodef-appeared {
		visibility: visible;
		
		.qodef-row-bg-text .qodef-char{
			opacity: 1;
			@include qodefTransform(translate3d(0, 0, 0));
			@include qodefTransitionTransform(.6s cubic-bezier(.05,.43,.04,.87), opacity .4s cubic-bezier(0.19, 0.5, 0.46, 0.88));
		}
	}
