/* ==========================================================================
   #Item With Animation styles - start
   ========================================================================== */

.qodef-item-with-animation {
	visibility: hidden;

	.touch & {
		visibility: visible;
	}
	
	.no-touch & {
		visibility: visible;
		opacity: 0;

		&.qodef-appeared {
			opacity: 1;
