/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 35px;
	
	.qodef-widget-title {
		margin: 0 0 30px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        .qodef-widget-title {
            margin: 0 0 20px;
        }

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $additional-color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .qodef-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        .qodef-widget-title {
            margin: 0 0 7px;
        }

        form {
            @include qodefRelativeHolderLayout();
            clear: both;

            > div {
                @include qodefRelativeHolderLayout();
                border-bottom: 1px solid $default-heading-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 400;
            font-size: 14px;
            line-height: 66px;
            color: $default-heading-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 28px);
            padding: 10px 0;
	
	        @include qodefPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 28px;
            padding: 10px 0;
            font-size: 18px;
            text-align: right;
            color: $default-heading-color;
            cursor: pointer;
            @include qodefTransition(color .2s ease-in-out);

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;

                &:before {
                    display: block;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        .qodef-widget-title {
            margin: 0 0 20px;
        }

        ul {

            li {
                margin: 0;
                padding: 2px 0;
                font-size: 13px;
                line-height: 25px;
                font-weight: 700;
                text-transform: uppercase;
                letter-spacing: .05em;

                a {
                    @include qodefMainHoverStyles();
                }

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 2px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 2px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_recent_entries {
	
	    .qodef-widget-title {
            margin: 0 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .qodef-widget-title {
            margin: 0 0 18px;
        }

        a {
            display: inline-block;
            vertical-align: middle;
            margin-right: 7px;
            font-family: inherit;
            font-size: 13px !important;
            line-height: 34px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: .05em;
            background-color: transparent;
            @include qodefMainHoverStyles();
        }
    }
    
    &.widget_media_image {
        margin-bottom: 20px;
        
        a, img {
            display: block;
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.qodef-page-header,
.qodef-top-bar,
.qodef-sticky-header,
.qodef-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.qodef-side-menu,
.qodef-footer-top-holder {

    .widget {
	    color: #fff;

	    &:last-child {
		    margin: 0 !important;
	    }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }

	    .qodef-widget-title {
		    color: #fff;
		    margin: 0 0 27px;
	    }

	    a {
		    color: #fff;
            z-index: 101;

		    &:hover {
			    color: $first-main-color;
		    }
	    }

        table thead tr,
        table tbody tr {
            border-color: #fff;
        }

        table tbody th {
            border-right-color: #fff;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: #fff;
            }

            td#today {
                color: $default-heading-color;
                background-color: $additional-color;
            }
        }

        &.widget_rss {

            .qodef-widget-title {

                .rsswidget {

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: #fff;
                }
            }

            input,
            button {
                color: #fff;
            }

            button {
                color: #fff;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_comments,
        &.widget_nav_menu {

            ul {

                li {

                    a {
                        color: #fff;
                    }
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #fff;
                border-color: #fff;

                option {
                    background-color: $default-heading-color;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #fff;
            }
        }
    }
}

.qodef-top-bar {

	a {
		color: #969696;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

.qodef-footer-top-holder {

    .widget {
        
        &.widget_media_image {
            margin-bottom: 15px;
        }
	
	    &.qodef-contact-form-7-widget {
		    margin-top: -20px !important;
	    }

        p {
            margin: 0 0 12px;
        }
    }
}

.qodef-footer-bottom-holder,
.qodef-fullscreen-menu-bottom {

    .widget {
        margin: 0;

        a {

            &:hover {
                color: #fff;
            }
        }

        @include ipad-landscape {
            margin: 0 0 10px;
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */